<template>
	<div class="item">
		<Carousel :options="swiperOption" :data="swiperData" :type="'meal'" @change="changeCarousel" />

		<div :ref="'meal' + item.id" class="meal" :key="item.id" v-for="item in serverMeal" :style="{ backgroundImage: 'url('+ item.headImg +')' }" >
			<div class="hospital">
				<div class="logo">
					<img :src="item.headLogo">
				</div>
				<div class="title">{{item.hospital}}</div>
				<div class="level">{{item.level}}</div>
			</div>

			<Setmeal :key="meal.id" v-for="meal in item.meal" :data="meal" @changeMeal="changeMeal" />
		</div>
	</div>
</template>

<script>
	import Carousel from '@/components/Carousel.vue'
	import Setmeal from '@/components/Setmeal.vue'

	export default {
		name: 'meal',
		components: { Carousel, Setmeal },
		data() {
			return {
				swiperOption: {
					swiperData: 'auto',
					pagination: {
						el: '.swiper-pagination',
						clickable: true
					}
				},
				swiperData: [],
				type: ['experience', 'cosmetology', 'regenerate'],
				service: [],
				serverMeal: []
			}
		},
		created() {
			fnCarousel(this);
			fnService(this);
			fnServerMeal(this);
			this.$emit('handlerTabbar', true);
		},
		methods: {
			changeCarousel(response) {
				if (response.type == 'meal') {
					this.$emit('handlerTabbar', false);
					sessionStorage.detail = JSON.stringify(this.service[response.index]);
					this.$router.push({ path: 'service', query: { type: this.type[response.index], id: this.swiperData[response.index].id }});
				}
			},
			changeMeal(item) {
				this.$emit('handlerTabbar', false);
				switch (item.id) {
					case '103':
					case '104':
					case '105':
					case '401':
					case '402':
					case '403':
					case '404':
					case '405':
					case '406':
					case '407':
					case '408':
					case '409':
					case '410':
					case '411':
						this.$router.push({ path: 'mdetail', query: { id: item.id }});
						break;
					default:
						this.$router.push({ path: 'meal-detail', query: { id: item.id }});
				}
			}
		},
		updated() {
			setTimeout(() => {
				let anchor = this.$refs['meal' + this.$route.query.id];
				document.documentElement.scrollTop = anchor[0].offsetTop - 40;
			}, 300)
		}
	}

	function fnCarousel(that, params) {
		that.$request.carousel(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.swiperData = result.list.service;
			}
		})
	}

	function fnService(that, params) {
		that.$request.service(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.service = result.list;
			}
		})
	}

	function fnServerMeal(that, params) {
		that.$request.serverMeal(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.serverMeal = result.list;
			}
		})
	}
</script>

<style lang="scss">
	@import './../styles/meal.scss'
</style>